body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  font-family: Papyrus;
  font-size: 3vw;
  color: white;
  text-align: center;
  overflow: hidden; }

#eyeball {
  position: relative; }

#cornea {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 130vh;
  height: 130vh;
  border-radius: 100% 0;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
  overflow: hidden; }

#cornea img {
  position: absolute;
  top: 90%;
  left: 70%;
  width: 170%;
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
  transform: translate(-50%, -50%) rotate(-45deg); }

#pupil {
  position: fixed;
  background-color: black;
  top: 50%;
  left: 49.5%;
  height: 5vh;
  width: 5vh;
  border-radius: 100%;
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
  transform: translate(-50%, -50%) rotate(-45deg);
  opacity: 0; }

#eyelid {
  background-color: transparent;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 130vh;
  height: 130vh;
  border-radius: 100% 0;
  border: 999px solid black;
  box-sizing: border-box;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg); }

#eyelashes {
  background-color: transparent;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 130vh;
  height: 130vh;
  border-radius: 100% 0;
  border: 2px solid yellow;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg); }

#lower-eyelid {
  background-color: black;
  position: fixed;
  top: 55%;
  left: 50%;
  width: 130vh;
  height: 130vh;
  border-radius: 100% 0;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
  border-bottom: 5px solid yellow;
  z-index: -1;
  opacity: 0; }

#upper-eyelid {
  background-color: black;
  position: fixed;
  top: 45%;
  left: 50%;
  width: 130vh;
  height: 130vh;
  border-radius: 100% 0;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
  border-top: 5px solid yellow;
  opacity: 0; }

#quote {
  position: fixed;
  top: 50%;
  left: 50%;
  color: white;
  display: none;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

#content {
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  color: white;
  font-size: 2vw;
  display: none;
  text-align: center;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

#stuff {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
  transform: translate(-50%);
  width: 60%;
  height: 100%; }

.link {
  position: absolute;
  width: 20%;
  color: white;
  display: none;
  z-index: 3; }

#upper-left {
  top: 0;
  left: 0;
  margin-top: 6%;
  -webkit-transform: rotate(-35deg);
  transform: rotate(-35deg); }

#upper-right {
  top: 0;
  right: 0;
  margin-top: 6%;
  -webkit-transform: rotate(35deg);
  transform: rotate(35deg); }

#lower-left {
  bottom: 0;
  left: 0;
  margin-bottom: 6%;
  -webkit-transform: rotate(35deg);
  transform: rotate(35deg); }

#lower-right {
  bottom: 0;
  right: 0;
  margin-bottom: 6%;
  -webkit-transform: rotate(-35deg);
  transform: rotate(-35deg); }

#cats {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 20%;
  opacity: 0;
  z-index: -5; }

button {
  background: none;
  border: none;
  outline: none;
  padding: 0;
  font: inherit;
  color: white;
  cursor: pointer; }

#music {
  position: fixed;
  bottom: 0;
  left: 1.5%;
  z-index: 5; }

#music-off {
  position: fixed;
  bottom: 0;
  left: 1.5%;
  display: none;
  z-index: 5; }

.speaker {
  height: 5vh;
  width: 5vh; }

